<template>
    <div class="logincc_layer">
        <header class="login-header-bar clearfix">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="login-title">收藏</h2>
        </header>
      <scroller
                :on-refresh="refresh"
                :on-infinite="infinite"
                ref="my_scroller"

      >
        <div class="personal-sc-box clearfix">
            <ul>
                <li class="tiezi-shouc-item" v-for="(item,index) in tieZiData" v-show="tieZiData.length>0">
                    <div class="line">

                    </div>
                    <div class="who-tiezi-item1 clearfix">
                        <img v-lazy="item.post.avatar" alt="" class="pc-shou-pic">
                        <div class="who-publish-box">
                            <p class="who-publish-name">
                              {{item.post.author}}
                            </p>
                            <div class="who-publish-times">
                                <span class="who-publish-time">
                                     发表于{{item.post.created_at | filterTime}}
                                </span>
                                <span class="who-publish-tag">
                                    来自标签：
                                    <span v-for="(tag,index) in item.post.tags">{{tag.name}}<span v-show="(index+1)!==item.post.tags.length">、</span></span>
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="who-tiezi-item2">
                        <router-link :to="'/tiezidetail/'+item.post.id" v-if="item.post.article_type===1 || item.post.article_type==='1'" class="who-tiezi-info">
                            <p class="tiezi-title">
                              {{item.post.title}}
                            </p>
                            <p class="shouc-tiezi-content" v-html="item.post.body">
                              {{item.post.body}}
                            </p>
                            <div class="line-one">

                            </div>
                        </router-link>
                    </div>
                    <div class="who-tiezi-item3 clearfix">
                        <div class="tiezi-read-box">
                            <i class="read-icon"></i>
                            <span class="tiezi-read-num">
                               {{item.post.article_views_count}}
                            </span>
                        </div>
                        <div class="tiezi-zanfh-box clearfix">
                            <div class="tiezi-hui-box">
                                <span class="tiezi-hui-icon">
                                </span>
                                <span class="tiezi-hui-text">
                                   {{item.post.article_comments_count}}
                                </span>
                            </div>
                            <div class="tiezi-zan-box">
                                <span class="tiezi-zan-icon">
                                </span>
                                <span class="tiezi-zan-text">
                                    {{item.post.article_likes_count}}
                                </span>
                            </div>
                        </div>
                    </div>
                   <!-- <div class="line">

                    </div>
                    <div class="tiezi-delete-box">
                        <div class="tiezi-delete-top" v-show="item.post.status ===2 || item.post.status ==='2'">
                            抱歉此帖子已被删除
                        </div>
                        <div class="line-one">

                        </div>
                        <a class="tiezi-delete-bottom" v-show="item.post.status ===1 || item.post.status ==='1'" @click="deleteShouC($event,item.post.id,item.post.status)">
                            取消收藏
                        </a>
                    </div>-->
                </li>

            </ul>
            <ul class="news-item-box shouc-news-item">
            <li  track-by="$index" v-for="(item,index) in newsData" class="news-item" v-show="item.post.status===1 || item.post.status==='1'">
              <router-link  class="news-item-link" :to="'/newsdetail/'+item.post.id">
                <img v-lazy="item.post.thu_image" alt="" class="news-icon">
                <div class="news-info-container">
                  <p class="news-title">
                    {{item.post.title}}
                  </p>
                  <div class="news_time_tag">
                    <span class="news_time">{{item.post.created_at}}</span>
                    <span class="news_tag">来自标签：<span v-for="(tag,index) in item.post.tags">{{tag.name}}<span v-show="(index+1)!==item.post.tags.length">、</span></span></span>

                  </div>
                  <div class="news_comment_read">
                    <span class="news_comment">评论{{item.post.article_comments_count}}</span>
                    <span class="news_read">阅读量{{item.post.article_views_count}}</span>
                  </div>
                </div>
              </router-link>
            </li>
          </ul>
        </div>
      </scroller>
    </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import Api from '../../../api/api';
  import {delCollect} from '../../../api/pcencollect';
  import {getCollectInfo} from '../../../api/pcencollect';
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        //总页数
        totalCount:0,
        //第一页
        pageIndex:1,
        //每一页的数量
        pageSize:10,
        type:0,
        newDatas:[],
        tieZiData:[],
        newsData:[],
      }
    },
    filters:{
      filterTime(value){
        return value.split("T")[0];
      }
    },
    mounted(){
      this.getMineTieZiInfos(0,1);
    },
    methods:{
        deShouC($event,id,status){
          let token = localStorage.getItem('token');
           const _this = this;
           if(token){
             if(status === '2'){
             _this.showMsg('该收藏已经被删除!');
             }else {
               const _this = this;
               delCollect(Api.COLLECTIONS,id).then((res)=>{
                 if(res.status === 204){
                   _this.showMsg('恭喜您，已取消收藏!');
                   _this.$router.go(0);
                 }
               },(err)=>{
               _this.showMsg('取消收藏，请重试!');
               _this.$store.dispatch('hideLoading');
             })
             }
           }else{
           this.$router.push({path: '/login', replace: true});
           }
          $event.preventDefault();
        },
        deleteShouC($event,id,status){
          const _this = this;
          MessageBox({
            title: '提示',
            message: '确定取消收藏吗?',
            showCancelButton: true
          });
          MessageBox.confirm('确定取消收藏吗?').then(action => {
            _this.deShouC($event,id,status);
          });

      },
      getMineTieZiInfos(type,page){
        const that = this;
        /*this.$http.get(`${Api.MYTIEZI}${localStorage.getItem('userId')}/collections/`,{
          params:{
            type:0,
            page:page,
            page_size:that.pageSize,
          }
        })*/
        getCollectInfo(`${Api.MYTIEZI}${localStorage.getItem('userId')}/collections/`,page,that.pageSize).then((res)=>{
          if(res.status === 200){
            that.initTieZiData(type,res);
          }else{
            that.dealErrInfo();
          }
        },(err)=>{
          that.dealErrInfo();
        });
      },
      dealErrInfo(){
        this.$store.dispatch('hideLoading');
        this.showMsg('获取数据失败，请重试');
      },
      initTieZiData(type,res){
          console.log("获取到的收藏数据是:",res);
        const _this = this;
        _this.totalCount = res.data.data.total;
        if(res.data!==null && res.data.data.detail.length>0){
          let tempArr = [];
          tempArr = res.data.data.detail;
          if(type===0){
            _this.newDatas = tempArr;
          }
          //下拉刷新
          else if(type===1){
            _this.newDatas=tempArr.splice(0,_this.pageSize);
          }else if(type === 2){ //上拉加载
            _this.newDatas = _this.newDatas.concat(tempArr);
          }

          const len = _this.newDatas.length;
          _this.newsData = [];
          _this.tieZiData = [];
          for(let i=0;i<len;i++) {
            //是帖子
            if (parseInt(_this.newDatas[i].post.article_type) === 1 && parseInt(_this.newDatas[i].post.status) === 1) {
              _this.tieZiData.push(_this.newDatas[i]);
            } else if (parseInt(_this.newDatas[i].post.article_type) === 0 && parseInt(_this.newDatas[i].post.status) === 1) {
              _this.newsData.push(_this.newDatas[i]);
            }
          }

        }else{
          _this.showMsg('当前暂无数据!');
        }
      },
      showMsg(str){
        this.$toast({
          message: str,
          duration: 2000,
        });
      },
      goTieZiDetail($event){
        this.$router.push({path: '/tiezidetail', replace: true});
        $event.preventDefault();
      },
      goWriteTiePage($event){
        this.$router.push({path: '/writetie', replace: true});
        $event.preventDefault();
      },
      infinite(done){
        const that = this;
        that.pageIndex++;
        that.type = 2;
        setTimeout(() => {
          //load more over
          if(that.pageIndex>that.totalCount){
            done(true);
          }else{
            done();
            that.getMineTieZiInfos(that.type,that.pageIndex);
          }
        }, 1500)
      },
      /**
       * 刷新
       * @param done
       */
      refresh(done){
        const that = this;
        that.pageIndex = 1;
        that.type = 1;
        setTimeout(() => {
          that.getMineTieZiInfos(that.type,that.pageIndex);
          done()
        }, 1500)
      },
    }

  }
</script>
<style scoped>
    @import "../../../assets/css/pcenshouchang.css";
    @import "../../../assets/css/index.css";
    body {
        background-color: #EDEEF0;
    }
</style>



